@import "../styles/variables";

.container {
    display: flex;
    padding: $margin-elements $margin-elements*2;
    background-color: $color-base;
    justify-content: space-between;
}

.navBar {
    display: flex;
}

.navButton {
    composes: button from "../styles/ui-kit.scss";
    flex: auto 0 0;
    margin-right: $margin-elements;
    &.selected {
        background: $color-highlight;
    }
}

.dataEntry {
    display: flex;
    input {
        outline: none;
        border: 1px solid darken($color-base, 20);
        border-radius: 3px;
        margin-right: $margin-elements;
        background: darken($color-base, 10);
        color: $color-primary;
        // hide the input[type=number] spinners
        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button {
            -webkit-appearance: none; 
            margin: 0;
        }
    }

    &.disabled {
        opacity: 0.5;
        pointer-events: none;
    }
}

.addButton {
    composes: button from "../styles/ui-kit.scss";
}
